import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { ParagraphProperties, ParagraphChildren } from '@ali/4ever-plugin-paragraph';
import { FoldableHeadingData } from './heading1';

export default class Heading6 extends Block<FoldableHeadingData, ParagraphChildren> {
  static TYPE = 'heading-6';

  static create<SD = FoldableHeadingData, SN extends BlockChildren = ParagraphChildren>(
    properties: Heading6 | ParagraphProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Heading6.create` does not accept string');
    }
    if (Heading6.isHeading6(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Heading6.TYPE,
    });
  }

  static isHeading6(maybeHeading6: any): maybeHeading6 is Heading6 {
    return Block.isBlock(maybeHeading6) && maybeHeading6.type === Heading6.TYPE;
  }

}
